<template>
  <div id="location">
    <Form @send-location="useLocation" @send-query="useQuery" />
    <!-- <div ref="mapRef" class="map"></div> -->
    <div id="dashboard">
      <div class="tomtom container" id="tomtom">
        <div class="row text-center">
          <div class="col-sm-12 col-lg-4">
            <div id="info" class="info">
              <img class="" src="@/assets/img/geocoder/geocoder.png" alt="geocoder" />
            </div>
          </div>
          <div class="col-sm-12 col-lg-8">
            <div id="map" class="map">
              <div class="coordinates" id="coordinates"></div>
            </div>
          </div>
        </div>
      </div>

      <div v-if="search" class="open_meteo container" id="open_meteo">
        <div class="row mb-3 top">
          <div class="col-12">
            <h2>Forecast Weather</h2>
            <h4>
              <a href="https://open-meteo.com/" target="blank">by Open-Meteo</a>
            </h4>
          </div>
        </div>
        <div class="row mb-3 forecast">
          <div class="col-12">
            <div class="row weather">
              <div class="left col-sm-12 col-lg-7 tile">
                <span class="w_key title">Todays Highlights</span>
                <div class="highlights">
                  <div class="today">
                    <h5>Sunrise & Sunset</h5>
                    <div class="info">
                      <div class="sunrise">
                        <img src="@/assets/img/geocoder/svg/sun_sunrise.svg" alt="" />
                        <p class="">
                          {{
                            getHoursAndMinutes(weather.data.daily.sunrise[0])
                          }}
                        </p>
                      </div>
                      <div class="sunset">
                        <img src="@/assets/img/geocoder/svg/sun_sunset.svg" alt="" />
                        <p class="">
                          {{ getHoursAndMinutes(weather.data.daily.sunset[0]) }}
                        </p>
                      </div>
                    </div>
                  </div>
                  <div class="today">
                    <h5>Min & Max</h5>
                    <div class="info">
                      <div class="min">
                        <img src="@/assets/img/geocoder/svg/min.svg" alt="temp min" />
                        <p class="position-relative">
                          {{ weather.data.daily.temperature_2m_min[0] }}<span class="celsius">C</span>
                        </p>
                      </div>
                      <div class="max">
                        <img src="@/assets/img/geocoder/svg/max.svg" alt="temp max" />
                        <p class="position-relative">
                          {{ weather.data.daily.temperature_2m_max[0] }}<span class="celsius">C</span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <div class="highlights">
                  <div class="today">
                    <h5>Precipitation Probability</h5>
                    <div class="info">
                      <div class="rain_prob">
                        <img src="@/assets/img/geocoder/svg/percentage_precipitation.svg" alt="" />
                        <p class="position-relative">
                          {{
                            weather.data.daily.precipitation_probability_max[0]
                          }}<span class="celsius">%</span>
                        </p>
                      </div>
                    </div>
                  </div>
                  <div class="today">
                    <h5>Wind Direction</h5>
                    <div class="info">
                      <div class="wind">
                        <img src="@/assets/img/geocoder/svg/wind.svg" alt="" />
                        <p class="position-relative">
                          {{ weather.data.daily.winddirection_10m_dominant[0]
                          }}<span class="celsius"></span>
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div class="right col-sm-12 col-lg-4 offset-lg-1">
                <div class="day">
                  <h2>{{ getDayName(weather.data.daily.time[0], "en-EN") }}</h2>
                  <div class="tile">
                    <div class="address" id="address">
                      <h3>
                        {{
                          this.location.data.addresses[0].address.municipality
                        }}
                      </h3>
                      <h5>
                        {{
                          this.location.data.addresses[0].address
                            .countrySecondarySubdivision
                        }},
                        {{
                          this.location.data.addresses[0].address
                            .countrySubdivision
                        }},
                        {{
                          this.location.data.addresses[0].address.countryCode
                        }}
                      </h5>
                    </div>
                    <hr />
                    <span class="w_key">Now: {{ getCurrentTime() }} </span>
                    <div class="now">
                      <span class="w_key temp">
                        {{ this.weather.data.current_weather.temperature }}
                        <span class="celsius">C</span>
                      </span>
                      <div v-if="this.weather.data.current_weather.is_day == 1">
                        <img v-if="this.weather.data.current_weather.weathercode == 0
                          " src="@/assets/img/geocoder/svg/sun.svg" alt="sunny" />
                        <img v-if="this.weather.data.current_weather.weathercode > 0 &&
                          this.weather.data.current_weather.weathercode < 4
                          " src="@/assets/img/geocoder/svg/cloudy_sunny.svg" alt="cloudy_sunny" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          4 &&
                          this.weather.data.current_weather.weathercode < 49
                          " src="@/assets/img/geocoder/svg/fog.svg" alt="foggy" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          49 &&
                          this.weather.data.current_weather.weathercode < 56
                          " src="@/assets/img/geocoder/svg/drizzle_rain.svg" alt="drizzle" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          56 &&
                          this.weather.data.current_weather.weathercode < 58
                          " src="@/assets/img/geocoder/svg/freezing_drizzle.svg" alt="freezing drizzle" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          57 &&
                          this.weather.data.current_weather.weathercode < 66
                          " src="@/assets/img/geocoder/svg/rain.svg" alt="rain" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          66 &&
                          this.weather.data.current_weather.weathercode < 68
                          " src="@/assets/img/geocoder/svg/freezing_rain.svg" alt="freezing rain" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          68 &&
                          this.weather.data.current_weather.weathercode < 76
                          " src="@/assets/img/geocoder/svg/snow.svg" alt="snowing" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          76 &&
                          this.weather.data.current_weather.weathercode < 78
                          " src="@/assets/img/geocoder/svg/snow_grains.svg" alt="snow graines" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          78 &&
                          this.weather.data.current_weather.weathercode < 83
                          " src="@/assets/img/geocoder/svg/heavy_rain.svg" alt="heavy rain" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          83 &&
                          this.weather.data.current_weather.weathercode < 87
                          " src="@/assets/img/geocoder/svg/snow_showers.svg" alt="snow showers" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          87 &&
                          this.weather.data.current_weather.weathercode < 96
                          " src="@/assets/img/geocoder/svg/thunder.svg" alt="thunderstorm" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          96 &&
                          this.weather.data.current_weather.weathercode < 99
                          " src="@/assets/img/geocoder/svg/hail_cloud.svg" alt="hail" />
                      </div>
                      <div v-else>
                        <img v-if="this.weather.data.current_weather.weathercode == 0
                          " src="@/assets/img/geocoder/svg/moon_1.svg" alt="moon" />
                        <img v-if="this.weather.data.current_weather.weathercode > 0 &&
                          this.weather.data.current_weather.weathercode < 4
                          " src="@/assets/img/geocoder/svg/cloudy_moon.svg" alt="cloudy_moon" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          4 &&
                          this.weather.data.current_weather.weathercode < 49
                          " src="@/assets/img/geocoder/svg/moon_fog.svg" alt="foggy moon" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          49 &&
                          this.weather.data.current_weather.weathercode < 56
                          " src="@/assets/img/geocoder/svg/drizzle_rain.svg" alt="drizzle" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          56 &&
                          this.weather.data.current_weather.weathercode < 58
                          " src="@/assets/img/geocoder/svg/freezing_drizzle.svg" alt="freezing drizzle" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          57 &&
                          this.weather.data.current_weather.weathercode < 66
                          " src="@/assets/img/geocoder/svg/moon_rain.svg" alt="rain" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          66 &&
                          this.weather.data.current_weather.weathercode < 68
                          " src="@/assets/img/geocoder/svg/freezing_rain.svg" alt="freezing rain" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          68 &&
                          this.weather.data.current_weather.weathercode < 76
                          " src="@/assets/img/geocoder/svg/moon_snow.svg" alt="snowing" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          76 &&
                          this.weather.data.current_weather.weathercode < 78
                          " src="@/assets/img/geocoder/svg/snow_grains.svg" alt="snow graines" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          78 &&
                          this.weather.data.current_weather.weathercode < 83
                          " src="@/assets/img/geocoder/svg/moon_rain.svg" alt="heavy rain" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          83 &&
                          this.weather.data.current_weather.weathercode < 87
                          " src="@/assets/img/geocoder/svg/moon_storm.svg" alt="snow showers" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          87 &&
                          this.weather.data.current_weather.weathercode < 96
                          " src="@/assets/img/geocoder/svg/thunder.svg" alt="thunderstorm" />
                        <img v-if="this.weather.data.current_weather.weathercode >=
                          96 &&
                          this.weather.data.current_weather.weathercode < 99
                          " src="@/assets/img/geocoder/svg/hail_cloud.svg" alt="hail" />
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row mb-5 forecast">
          <div class="col-12">
            <div class="row weather">
              <div class="center col tile">
                <span class="w_key title">Today at</span>
                <div class="highlights d-flex justify-content-center align-items-center flex-wrap">
                  <div class="hourly" v-for="(hour, index) in this.day[0]" :key="hour">
                    <div class="hour">
                      <h5 v-if="index >= 0 && index < 10" class="m-0">
                        {{ "0" + index + ":00" }}
                      </h5>
                      <h5 v-else class="m-0">{{ index + ":00" }}</h5>
                      <div class="hour_time">
                        <!-- day -->
                        <div v-if="hour.isDay == 1">
                          <img v-if="hour.weathercode == 0" src="@/assets/img/geocoder/svg/sun.svg" alt="sunny" />
                          <img v-if="hour.weathercode > 0 && hour.weathercode < 4"
                            src="@/assets/img/geocoder/svg/cloudy_sunny.svg" alt="cloudy_sunny" />
                          <img v-if="hour.weathercode >= 4 && hour.weathercode < 49
                            " src="@/assets/img/geocoder/svg/fog.svg" alt="foggy" />
                          <img v-if="hour.weathercode >= 49 && hour.weathercode < 56
                            " src="@/assets/img/geocoder/svg/drizzle_rain.svg" alt="drizzle" />
                          <img v-if="hour.weathercode >= 56 && hour.weathercode < 58
                            " src="@/assets/img/geocoder/svg/freezing_drizzle.svg" alt="freezing drizzle" />
                          <img v-if="hour.weathercode >= 57 && hour.weathercode < 66
                            " src="@/assets/img/geocoder/svg/rain.svg" alt="rain" />
                          <img v-if="hour.weathercode >= 66 && hour.weathercode < 68
                            " src="@/assets/img/geocoder/svg/freezing_rain.svg" alt="freezing rain" />
                          <img v-if="hour.weathercode >= 68 && hour.weathercode < 76
                            " src="@/assets/img/geocoder/svg/snow.svg" alt="snowing" />
                          <img v-if="hour.weathercode >= 76 && hour.weathercode < 78
                            " src="@/assets/img/geocoder/svg/snow_grains.svg" alt="snow graines" />
                          <img v-if="hour.weathercode >= 78 && hour.weathercode < 83
                            " src="@/assets/img/geocoder/svg/heavy_rain.svg" alt="heavy rain" />
                          <img v-if="hour.weathercode >= 83 && hour.weathercode < 87
                            " src="@/assets/img/geocoder/svg/snow_showers.svg" alt="snow showers" />
                          <img v-if="hour.weathercode >= 87 && hour.weathercode < 96
                            " src="@/assets/img/geocoder/svg/thunder.svg" alt="thunderstorm" />
                          <img v-if="hour.weathercode >= 96 && hour.weathercode < 99
                            " src="@/assets/img/geocoder/svg/hail_cloud.svg" alt="hail" />
                        </div>
                        <!-- night -->
                        <div v-else>
                          <img v-if="hour.weathercode == 0" src="@/assets/img/geocoder/svg/moon_1.svg" alt="moon" />
                          <img v-if="hour.weathercode > 0 && hour.weathercode < 4"
                            src="@/assets/img/geocoder/svg/cloudy_moon.svg" alt="cloudy_moon" />
                          <img v-if="hour.weathercode >= 4 && hour.weathercode < 49
                            " src="@/assets/img/geocoder/svg/moon_fog.svg" alt="foggy moon" />
                          <img v-if="hour.weathercode >= 49 && hour.weathercode < 56
                            " src="@/assets/img/geocoder/svg/drizzle_rain.svg" alt="drizzle" />
                          <img v-if="hour.weathercode >= 56 && hour.weathercode < 58
                            " src="@/assets/img/geocoder/svg/freezing_drizzle.svg" alt="freezing drizzle" />
                          <img v-if="hour.weathercode >= 57 && hour.weathercode < 66
                            " src="@/assets/img/geocoder/svg/moon_rain.svg" alt="rain" />
                          <img v-if="hour.weathercode >= 66 && hour.weathercode < 68
                            " src="@/assets/img/geocoder/svg/freezing_rain.svg" alt="freezing rain" />
                          <img v-if="hour.weathercode >= 68 && hour.weathercode < 76
                            " src="@/assets/img/geocoder/svg/moon_snow.svg" alt="snowing" />
                          <img v-if="hour.weathercode >= 76 && hour.weathercode < 78
                            " src="@/assets/img/geocoder/svg/snow_grains.svg" alt="snow graines" />
                          <img v-if="hour.weathercode >= 78 && hour.weathercode < 83
                            " src="@/assets/img/geocoder/svg/moon_rain.svg" alt="heavy rain" />
                          <img v-if="hour.weathercode >= 83 && hour.weathercode < 87
                            " src="@/assets/img/geocoder/svg/moon_storm.svg" alt="snow showers" />
                          <img v-if="hour.weathercode >= 87 && hour.weathercode < 96
                            " src="@/assets/img/geocoder/svg/thunder.svg" alt="thunderstorm" />
                          <img v-if="hour.weathercode >= 96 && hour.weathercode < 99
                            " src="@/assets/img/geocoder/svg/hail_cloud.svg" alt="hail" />
                        </div>
                        <p class="m-0">{{ hour.temperature }}c</p>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="row mb-5 forecast">
          <div class="col-12">
            <div class="row weather">
              <div class="center col tile">
                <span class="w_key title">Next Days</span>
                <div class="highlights d-flex justify-content-center align-items-center flex-wrap">
                  <div class="hourly h-auto w_days" v-for="day in filteredNextDays" :key="day.id">
                    <div class="hour">
                      <h5 class="d_name">
                        {{ getDayName(day.time, "en-EN") }}
                      </h5>
                      <div class="hour_time">
                        <img v-if="day.weathercode == 0" src="@/assets/img/geocoder/svg/sun.svg" alt="sunny" />
                        <img v-if="day.weathercode > 0 && day.weathercode < 4"
                          src="@/assets/img/geocoder/svg/cloudy_sunny.svg" alt="cloudy_sunny" />
                        <img v-if="day.weathercode >= 4 && day.weathercode < 49" src="@/assets/img/geocoder/svg/fog.svg"
                          alt="foggy" />
                        <img v-if="day.weathercode >= 49 && day.weathercode < 56"
                          src="@/assets/img/geocoder/svg/drizzle_rain.svg" alt="drizzle" />
                        <img v-if="day.weathercode >= 56 && day.weathercode < 58"
                          src="@/assets/img/geocoder/svg/freezing_drizzle.svg" alt="freezing drizzle" />
                        <img v-if="day.weathercode >= 57 && day.weathercode < 66" src="@/assets/img/geocoder/svg/rain.svg"
                          alt="rain" />
                        <img v-if="day.weathercode >= 66 && day.weathercode < 68"
                          src="@/assets/img/geocoder/svg/freezing_rain.svg" alt="freezing rain" />
                        <img v-if="day.weathercode >= 68 && day.weathercode < 76" src="@/assets/img/geocoder/svg/snow.svg"
                          alt="snowing" />
                        <img v-if="day.weathercode >= 76 && day.weathercode < 78"
                          src="@/assets/img/geocoder/svg/snow_grains.svg" alt="snow graines" />
                        <img v-if="day.weathercode >= 78 && day.weathercode < 83"
                          src="@/assets/img/geocoder/svg/heavy_rain.svg" alt="heavy rain" />
                        <img v-if="day.weathercode >= 83 && day.weathercode < 87"
                          src="@/assets/img/geocoder/svg/snow_showers.svg" alt="snow showers" />
                        <img v-if="day.weathercode >= 87 && day.weathercode < 96"
                          src="@/assets/img/geocoder/svg/thunder.svg" alt="thunderstorm" />
                        <img v-if="day.weathercode >= 96 && day.weathercode < 99"
                          src="@/assets/img/geocoder/svg/hail_cloud.svg" alt="hail" />
                      </div>
                      <div class="min_max">
                        <div>
                          <i class="fa-solid fa-caret-down" style="color: #0158ef"></i>
                          <span> {{ day.temperature_min }}</span>
                        </div>
                        <div>
                          <i class="fa-solid fa-caret-up" style="color: #c91818"></i>
                          <span> {{ day.temperature_max }}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import tt from "@tomtom-international/web-sdk-maps";
import axios from "axios";
import Form from "@/components/Geocoder/Form.vue";

export default {
  name: "MapComp",
  components: {
    Form,
  },
  data() {
    return {
      welcomeGeocoder: false,
      detailsAreVisible: false,
      search: false,
      // 12.5113300
      // 41.8919300
      query: "",
      long: null,
      lat: null,
      key: "TYMEGvpqbUgpY10zZu3mHGsSeeLdlngD",
      location: {},
      weather: {},
      hourly: {},
      day: [],
      nextDays: {},
    };
  },
  computed: {
    filteredNextDays() {
      return this.nextDays.filter((item) => item.id != 0);
    },
  },
  mounted() {
    this.defaultMap();
  },
  methods: {
    defaultMap() {
      var map = tt.map({
        key: this.key,
        container: "map",
      });
      map.addControl(new tt.FullscreenControl());
      map.addControl(new tt.NavigationControl());
    },
    useLocation(long, lat) {
      this.long = long;
      this.lat = lat;
      this.getMap();
    },
    useQuery(query) {
      this.query = query;
      this.readQuery();
    },
    readQuery() {
      axios
        .get(
          `https://api.tomtom.com/search/2/poiSearch/${this.query}.json?key=${this.key}`
        )
        .then((result) => {
          // console.log(result);
          this.lat = result.data.results[0].position.lat;
          this.long = result.data.results[0].position.lon;
          this.getMap();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getMap() {
      var map = tt.map({
        key: this.key,
        container: "map",
        center: [this.long, this.lat],
        zoom: 13,
      });
      map.addControl(new tt.FullscreenControl());
      map.addControl(new tt.NavigationControl());
      this.addMarker(map);
    },
    addMarker(map) {
      var location = [this.long, this.lat];
      var popupOffsets = 25;
      var marker = new tt.Marker().setLngLat(location).addTo(map);
      var popup = new tt.Popup({ offset: popupOffsets });
      this.reverseGeocoding(marker, popup);
      marker.setPopup(popup).togglePopup();
      this.getMeteo();
    },
    reverseGeocoding(marker, popup) {
      axios
        .get(
          `https://api.tomtom.com/search/2/reverseGeocode/${this.lat},${this.long}.json?key=${this.key}&radius=100`
        )
        .then((result) => {
          // console.log(result);
          this.location = result;
          popup.setHTML(result.data.addresses[0].address.freeformAddress);

          // Aggiunge in un div l'indirizzo
          // var address = document.getElementById("address");
          // address.innerHTML = `<h3>${result.data.addresses[0].address.municipality}</h3>
          //  <h5>${result.data.addresses[0].address.countrySecondarySubdivision}, ${result.data.addresses[0].address.countrySubdivision}, ${result.data.addresses[0].address.countryCode}</h5>`;
          // result.data.addresses[0].address.freeformAddress +
          // ", " +
          // result.data.addresses[0].address.country;
          var coordinates = document.getElementById("coordinates");
          coordinates.innerHTML = `<i class='fa-solid fa-map-pin' style='color: #000000;''> <span> Longitude: ${this.long} -  Latitude: ${this.lat}</span>`;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    getMeteo() {
      this.search = true;
      this.weather = {};
      // this.days = [];
      axios
        .get(
          `https://api.open-meteo.com/v1/forecast?latitude=${this.lat}&longitude=${this.long}&hourly=temperature_2m,relativehumidity_2m,precipitation_probability,precipitation,rain,weathercode,uv_index,uv_index_clear_sky,is_day,cape,freezinglevel_height&daily=weathercode,temperature_2m_max,temperature_2m_min,apparent_temperature_max,apparent_temperature_min,sunrise,sunset,uv_index_max,uv_index_clear_sky_max,precipitation_sum,rain_sum,showers_sum,snowfall_sum,precipitation_hours,precipitation_probability_max,windspeed_10m_max,windgusts_10m_max,winddirection_10m_dominant,shortwave_radiation_sum&timezone=auto&current_weather=true`
        )
        .then((result) => {
          this.weather = result;
          // console.log(this.weather);

          // hourly obj
          const idHour = Array.from(Array(168).keys());
          const isDay = result.data.hourly.is_day;
          const temp = result.data.hourly.temperature_2m;
          const codeHour = result.data.hourly.weathercode;
          this.hourly = this.createObjHourlyFromArrays(
            idHour,
            isDay,
            temp,
            codeHour
          );
          // day array
          this.day = this.splitDailyArray(this.hourly);
          // console.log(this.day);

          // nextDays obj
          const idDay = Array.from(Array(7).keys());
          const tempMax = result.data.daily.apparent_temperature_max;
          const tempMin = result.data.daily.apparent_temperature_min;
          const time = result.data.daily.time;
          const codeDay = result.data.daily.weathercode;
          this.nextDays = this.createObjDailyFromArrays(
            idDay,
            tempMax,
            tempMin,
            time,
            codeDay
          );
          // console.log(this.nextDays);
        })
        .catch((err) => {
          console.log(err);
        });
    },
    toggleDetails(id) {
      // console.log(id);
      var element = document.getElementById(id);
      element.classList.toggle("visible");
      element.classList.toggle("not_visible");
    },
    getDayName(dateStr, locale) {
      var date = new Date(dateStr);
      return date.toLocaleDateString(locale, { weekday: "long" });
      // var dateStr = '2023-06-12';
      // var day = getDayName(dateStr, "en-EN");
      // console.log(day);
    },
    getHoursAndMinutes(date) {
      var d = new Date(date);
      return d.getHours() + ":" + d.getMinutes();
    },
    getActualTemp() {
      var d = new Date();
      var dateIndex = d.getHours();
      return this.weather.data.hourly.temperature_2m[dateIndex];
    },
    getTemp(index) {
      return this.weather.data.hourly.temperature_2m[index];
    },
    getCurrentTime() {
      var date = new Date(this.weather.data.current_weather.time);
      return date.toLocaleString();
    },
    splitDailyArray(array) {
      const nums = array;
      // Get number of subarrays
      const numsPerGroup = Math.ceil(nums.length / 7);
      // Create n subarrays based on number of item
      const result = new Array(7)
        // Make sure each element isn't empty
        .fill("")
        // For each group, grab the right `slice` of the input array
        .map((_, i) => nums.slice(i * numsPerGroup, (i + 1) * numsPerGroup));
      return result;
    },
    createObjHourlyFromArrays(array1, array2, array3, array4) {
      const arrayOfObjects = array1.map((currentValue, index) => {
        return {
          id: currentValue,
          temperature: array3[index],
          weathercode: array4[index],
          isDay: array2[index],
        };
      });
      return arrayOfObjects;
    },
    createObjDailyFromArrays(array1, array2, array3, array4, array5) {
      // idDay,tempMax, tempMin, time, codeDay
      const arrayOfObjects = array1.map((currentValue, index) => {
        return {
          id: currentValue,
          temperature_max: array2[index],
          temperature_min: array3[index],
          time: array4[index],
          weathercode: array5[index],
        };
      });
      return arrayOfObjects;
    },
  },
};
</script>

